import { TDataLayer } from '@porsche/globaldatalayer-events/projects/pcom/all/types';
import { Component, Element, h, Host, JSX, Prop } from '@stencil/core';
import { ClickElementType, ClickElementTypes, EventAction, EventActions } from '../../utility/constants';
import { isString } from '../../utility/helper';
import MenuItemText from './menu-item-text';
import Icon from './menu-item-icon';
import { sendDataLayerEvent } from '../../services/analytics/data-layer-event-sender';

@Component({
  tag: 'phn-menu-item',
  styleUrl: 'menu-item.scss',
  scoped: true
})
export class MenuItem {
  @Prop() text = '';
  @Prop() analyticsId!: string;
  @Prop() subtext = '';
  @Prop() fontWeight = 'regular';
  @Prop() elementId = '';
  @Prop() size = 'inherit';
  @Prop() iconLeft = '';
  @Prop() iconRight = '';
  @Prop() link = '';
  @Prop() isLoading = false;
  @Prop() datalayerEventAction: EventAction = EventActions.NAVIGATION_CLICK;
  @Prop() customDatalayerProperties: TDataLayer = {};
  @Prop() target = '_self';
  @Prop() variant = '';
  @Prop() isModels = false;

  @Element() el!: HTMLPhnMenuItemElement;

  private sendDataLayerEvent(clickElementType: ClickElementType, elementId: string): void {
    sendDataLayerEvent({
      analyticsId: this.analyticsId,
      text: this.text,
      elementId,
      clickElementType,
      link: this.link,
      customDatalayerProperties: this.customDatalayerProperties,
      datalayerEventAction: this.datalayerEventAction
    });
  }

  private existsSlot(slotName: string): boolean {
    return this.el.querySelector(`[slot="${slotName}"]`) !== null;
  }

  render(): JSX.Element {
    const body = (
      <div class="phn-flex-row phn-align-center">
        {this.existsSlot('media') ? (
          <div class="media-container skeleton-item">
            <slot name="media" />
          </div>
        ) : null}
        <Icon isLoading={this.isLoading} icon={this.iconLeft} />
        {!this.isModels && isString(this.text) ? (
          <MenuItemText size={this.size} text={this.text} subtext={this.subtext} fontWeight={this.fontWeight} />
        ) : undefined}
        {this.existsSlot('suffix') ? (
          <div>
            <slot name="suffix" />
          </div>
        ) : null}
      </div>
    );

    if (isString(this.link)) {
      return (
        <Host class={this.variant}>
          <phn-p-link-pure
            class={`${isString(this.iconLeft) ? 'has-icon' : ''}`}
            icon={isString(this.iconRight) ? this.iconRight : 'none'}
            align-label="left"
            stretch={isString(this.iconRight) ? 'true' : 'false'}
            data-id={this.elementId}
            target={this.target}
            onClick={() => {
              this.sendDataLayerEvent(ClickElementTypes.Link, this.elementId);
            }}
          >
            <a href={this.link}>{body}</a>
          </phn-p-link-pure>
        </Host>
      );
    }

    return (
      <Host class={this.variant}>
        <phn-p-button-pure
          class={`${isString(this.iconLeft) ? 'has-icon' : ''}`}
          size="inherit"
          icon={isString(this.iconRight) ? this.iconRight : 'none'}
          align-label="left"
          stretch={isString(this.iconRight) ? 'true' : 'false'}
          data-id={this.elementId}
          onClick={() => {
            this.sendDataLayerEvent(ClickElementTypes.Button, this.elementId);
          }}
        >
          {body}
        </phn-p-button-pure>
      </Host>
    );
  }
}
