@import '../../scss';

@mixin model-skeleton() {
  &.skeleton {
    & .media-container.skeleton-item {
      width: p-px-to-rem(154px);
      height: p-px-to-rem(86px);
      border-radius: 4px;

      @include pds-media-query-min('xs') {
        width: p-px-to-rem(164px);
        height: p-px-to-rem(92px);
      }

      @include pds-media-query-min('s') {
        width: p-px-to-rem(150px);
        height: p-px-to-rem(85px);
      }

      @include pds-media-query-min('l') {
        width: p-px-to-rem(175px);
        height: p-px-to-rem(98px);
      }

      @include pds-media-query-min('xxl') {
        width: p-px-to-rem(200px);
        height: p-px-to-rem(112px);
      }

      &::slotted(img) {
        visibility: hidden;
      }
    }

    & .skeleton-item {
      width: p-px-to-rem(100px);

      @include pds-media-query-min('xs') {
        width: p-px-to-rem(124px);
      }

      @include pds-media-query-min('s') {
        width: p-px-to-rem(87px);
      }

      @include pds-media-query-min('l') {
        width: p-px-to-rem(124px);
      }
    }
  }
}

@mixin default-skeleton() {
  &.skeleton {
    & .skeleton-item {
      pointer-events: none;
      user-select: none;
      background: $pds-theme-light-contrast-low;
      border-radius: 4px;
    }

    & .skeleton-item * {
      visibility: hidden;
    }

    & .skeleton-item:empty::after,
    & .skeleton-item *:empty::after {
      content: "\00a0";
    }
  }
}

@mixin default-menu-item() {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @include pds-media-query-min('l') {
    border-bottom: 0;
  }

  phn-p-link-pure,
  phn-p-button-pure,
  phn-menu-item{
    box-sizing: border-box;
    padding: 0 8px;

    @include pds-media-query-min('l') {
      padding-left: 0;
      border-bottom: 0;
    }

    phn-p-text {
      font-size: p-px-to-rem(16px);

      @include pds-media-query-min-max('xs', "s") {
        font-size: p-px-to-rem(20px);
      }

      @include pds-media-query-min-max('s', 'l') {
        font-size: p-px-to-rem(18px);
      }

      @include pds-media-query-min('l') {
        font-size: p-px-to-rem(20px);
      }
    }

    &.has-icon {
      padding-left: 0;
    }

    .icon-container {
      position: relative;
      margin-right: p-px-to-rem(4px);

      width: p-px-to-rem(24px);
      height: p-px-to-rem(24px);

      @include pds-media-query-min-max('s', 'm') {
        height: p-px-to-rem(28px);
        width: p-px-to-rem(28px);
      }

      @include pds-media-query-min-max('m', 'xl') {
        width: p-px-to-rem(24px);
        height: p-px-to-rem(24px);
      }

      @include pds-media-query-min('xl') {
        width: p-px-to-rem(28px);
        height: p-px-to-rem(28px);
      }

      phn-p-icon {
        margin-right: p-px-to-rem(4px);
        width: p-px-to-rem(24px);

        @include pds-media-query-min-max('s', 'm') {
          width: p-px-to-rem(28px);
        }
      }
    }
  }
}

@mixin model-menu-item() {

  phn-p-link-pure,
  phn-p-button-pure,
  phn-menu-item{
    phn-p-text {
      font-size: p-px-to-rem(18px);

      @include pds-media-query-min('xs') {
        font-size: p-px-to-rem(20px);
      }

      @include pds-media-query-min('s') {
        font-size: p-px-to-rem(16px);
      }

      @include pds-media-query-min('l') {
        font-size: p-px-to-rem(18px);
      }

      @include pds-media-query-min('xxl') {
        font-size: p-px-to-rem(20px);
      }
    }
  }
}

@mixin dealer-details-menu-item() {

  phn-p-link-pure,
  phn-p-button-pure,
  phn-menu-item{

    phn-p-text {
      font-size: p-px-to-rem(16px);
    }

    .icon-container {
      phn-p-icon {
        margin-right: p-px-to-rem(4px);
        width: p-px-to-rem(24px);
      }
    }
  }
}

@mixin level1-menu-item() {

  phn-p-link-pure,
  phn-p-button-pure,
  phn-menu-item{
    phn-p-text {
      font-size: p-px-to-rem(20px);

      @include pds-media-query-min('xs') {
        font-size: p-px-to-rem(24px);
      }

      @include pds-media-query-min('s') {
        font-size: p-px-to-rem(18px);
      }

      @include pds-media-query-min('m') {
        font-size: p-px-to-rem(20px);
      }

      @include pds-media-query-min('l') {
        font-size: p-px-to-rem(22px);
      }

      @include pds-media-query-min('xxl') {
        font-size: p-px-to-rem(24px);
      }
    }
  }
}


:host {
  @include default-menu-item();
  @include default-skeleton();

  &.model-variant {
    @include model-menu-item();
    @include model-skeleton();
  }

  &.dealer-details-variant {
    @include dealer-details-menu-item();
  }

  &.level1-variant {
    @include level1-menu-item();
  }
}

div.text-limit {
  @include pds-media-query-min('base') {
    width: 220px;
  }

  @include pds-media-query-min('xs') {
    width: 100%;
  }

}
